h2 {
  font-size: 2.375rem;
  line-height: 130%;
  color: rgba(255, 255, 255, 1);
  .text_color {
    color: rgb(245, 216, 155);
  }
}
.btn-yellow {
  background: transparent;
  border: 0.03125rem solid rgb(245, 216, 155) !important;
  padding: 0.75rem !important;
  &:hover {
    span {
      background: rgb(255, 247, 229);
    }
  }
  &:active {
    span {
      background: rgb(113, 113, 113);
      color: rgb(255, 255, 255);
    }
  }
  span {
    min-width: unset;
    background: rgb(245, 216, 155);
    border-radius: 0.25rem;
    font-family: 'Cormorant';
    font-size: 1.125rem;
    line-height: 130%;
    color: rgb(51, 51, 51);
    padding: 1.125rem 2rem;
    overflow: unset;
  }
}
.slick-arrow {
  width: 3.5rem;
  height: 3.5rem;
  border: 0.0625rem solid rgb(245, 216, 155);
  border-radius: 0.25rem;
  &:hover {
    border-color: rgb(255, 255, 255);
  }
  svg {
    width: 1.5rem;
    height: auto;
  }
}
.popup {
  background: transparent;
  padding: 0;
  .form-wrap {
    max-width: 25.375rem;
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.navigation {
  padding: 3.125rem 0;
  ul {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li {
      font-family: 'Cormorant';
      font-size: 2rem;
      line-height: 130%;
      font-weight: 700;
      a:hover {
        color: rgb(245, 216, 155);
      }
    }
  }
}


@media screen and (max-width: 1600px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 1.5rem;
  }
  .btn-yellow {
    span {
      font-size: 1rem;
    }
  }
  .slick-arrow {
    width: 2.5rem;
    height: 2.5rem;
  }
}