.main-section {
  background: rgb(57, 56, 63);
  padding: 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-left: max(6rem, calc(100% - 1620px) / 2);
  }
  &__left {
    position: relative;
    z-index: 1;
    max-width: 40.625rem;
    width: 100%;
    padding: 6rem 2rem 12.5rem 0;
  }
  &__right {
    position: relative;
    flex-grow: 1;
  }
  .titles {
    h1:nth-child(2) {
      display: none;
    }
  }
  h1 {
    font-size: 6.625rem;
    line-height: 130%;
    color: rgb(255, 255, 255);
  }
  @media screen and (min-width: 1601px) {
    h1 {
      white-space: nowrap;
    }
  }
  p {
    font-size: 2rem;
    line-height: 130%;
    color: rgb(255, 255, 255);
    margin: 0.5rem 0 0 0;
    span {
      font-weight: 600;
      color: rgb(245, 216, 155);
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1.5rem;
    margin-top: 2.8125rem;
  }
  &__info {
    max-width: 18.4375rem;
    font-size: 1rem;
    line-height: 130%;
    color: rgb(255, 255, 255);
    span {
      font-weight: 600;
      color: rgb(245, 216, 155);
    }
  }
  &__img,
  &__img2 {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__img2 {
    display: none;
  }
  .tab {
    position: absolute;
    right: max(9rem, calc(100% - 1620px) / 2);
    bottom: 12.5rem;
    min-width: 24.375rem;
    display: flex;
    align-items: center;
    background: rgba(39, 39, 39, 0.68);
    border: 0.125rem solid rgb(245, 216, 155);
    backdrop-filter: blur(0.625rem);
    border-radius: 1rem;
    cursor: pointer;
    h2 {
      padding: 1.5rem 1.75rem;
    }
  }
  .tab:nth-child(2) {
    display: none;
  }
  &__img3 {
    width: 9.125rem;
    border-radius: 1rem;
    img {
      width: 100%;
    }
  }
  &--active {
    .titles {
      h1:nth-child(1) {
        display: none;
      }
      h1:nth-child(2) {
        display: block;
      }
    }
    .main-section__img {
      display: none;
    }
    .main-section__img2 {
      display: block;
    }
    .tab:nth-child(1) {
      display: none;
    }
    .tab:nth-child(2) {
      display: flex;
    }
  }
}
.section-group {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 6.25rem;
    left: 0;
    width: 100%;
    height: calc(100% + 17.1875rem);
    background-image: url("../img/section-group__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
}
.advantages {
  padding: 0;
  margin-top: -6.25rem;
  &__items {
    position: relative;
    max-width: 71.25rem;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 4rem / 3));
    gap: 2rem;
    background: rgba(39, 39, 39, 0.8);
    backdrop-filter: blur(0.5rem);
    padding: 3.125rem;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 1.25rem);
      height: calc(100% - 1.25rem);
      border: 0.03125rem solid rgb(245, 216, 155);
      pointer-events: none;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-img {
      position: relative;
      width: 6.75rem;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 4rem;
        height: 4rem;
        background: rgba(245, 216, 155, 0.65);
        filter: blur(1.5rem);
        border-radius: 50%;
        pointer-events: none;
      }
      img {
        position: relative;
        z-index: 1;
        width: 100%;
      }
    }
    &-text {
      font-size: 1.125rem;
      line-height: 130%;
      color: rgb(255, 255, 255);
      margin-top: 1.125rem;
      strong {
        display: block;
        font-weight: 700;
        color: rgb(245, 216, 155);
      }
    }
  }
  &__img {
    position: absolute;
    top: -6.25rem;
    left: 0;
    width: 25rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    top: -6.25rem;
    right: 0;
    width: 18.75rem;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.projects {
  padding: 9.375rem 0 4.6875rem;
  &__inner {
    display: flex;
    gap: 3.75rem;
  }
  &__left {
    position: relative;
    z-index: 2;
    max-width: 25rem;
    width: 100%;
  }
  &__right {
    position: relative;
    max-width: 45.625rem;
    .slick-list {
      overflow: unset;
    }
  }
  &__slider,
  &__slider2 {
    position: relative;
    z-index: 1;
  }
  &__slider-nav {
    display: flex;
    align-items: center;
    gap: 1.25rem;
    margin-top: 1.5rem;
  }
  .slick-list {
    margin: 0 -1rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    margin: 0 1em;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    & > div {
      height: 100%;
    }
  }
  .slick-active,
  .slick-active + .slick-slide,
  .slick-active + .slick-slide + .slick-slide,
  .slick-active + .slick-slide + .slick-slide + .slick-slide {
    opacity: 1;
    pointer-events: auto;
  }
  &__prev,
  &__prev2 {
    position: static;
  }
  &__next,
  &__next2 {
    position: static;
  }
  .pagingInfo {
    display: flex;
    align-items: flex-end;
    span:nth-child(1) {
      position: relative;
      display: flex;
      align-items: flex-end;
      font-family: 'Cormorant';
      font-size: 3.5rem;
      line-height: 105%;
      color: rgb(255, 255, 255);
      &:after {
        content: '/';
        display: inline-block;
      }
    }
    span:nth-child(2) {
      font-family: 'Cormorant';
      font-size: 2.5rem;
      line-height: 105%;
      color: rgb(181, 181, 181);
    }
  }
}
