.project-card {
  position: relative;
  height: 100%;
  display: flex !important;
  gap: 2rem;
  background: rgba(39, 39, 39, 0.95);
  backdrop-filter: blur(0.875rem);
  padding: 3.5rem;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 1.25rem);
    height: calc(100% - 1.25rem);
    border: 0.03125rem solid rgb(245, 216, 155);
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: -1.5rem;
    transform: translate(-50%, 0);
    width: calc(100% + 3.75rem);
    height: 1.5rem;
    background: rgba(51, 51, 51, 0.86);
    filter: blur(1.25rem);
    pointer-events: none;
  }
  .project-card__slider {
    width: 45%;
  }
  &__desc {
    width: 55%;
  }
  &__desc--active {
    .project-card__text {
      overflow: unset;
      display: unset;
    }
    .project-card__btn {
      span:nth-child(1) {
        display: none;
      }
      span:nth-child(2) {
        display: inline-block;
      }
    }
  }
  &__text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:14;
    -webkit-box-orient: vertical;
    p {
      font-size: 1rem;
      line-height: 120%;
      color: rgb(190, 190, 190);
      margin: 0;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }
  &__btn {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
    margin-top: 2rem;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: rgb(245, 216, 155);
      span {
        border-color: rgb(245, 216, 155);
      }
    }
    span:nth-child(2) {
      display: none;
    }
    span {
      border-bottom: 0.0625rem dashed rgb(255, 255, 255);
      padding-bottom: 0.1875rem;
    }
  }
  .project-card__slider-big {
    .slick-slide {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 2.125rem;
        height: 2.125rem;
        background-image: url("../img/magnifier.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        pointer-events: none;
      }
    }
  }
  .project-card__slider-small {
    margin-top: 1rem;
    .slick-list {
      margin: 0 -0.5rem;
    }
    .slick-track {
      display: flex;
      align-items: stretch;
    }
    .slick-slide {
      height: auto;
      margin: 0 0.5em;
      opacity: 1;
      pointer-events: auto;
      & > div {
        height: 100%;
      }
    }
    .project-card__img {
      height: 100%;
    }
    .slick-arrow {
      display: none;
    }
  }
  .slick-list {
    padding: 0 !important;
    overflow: hidden;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-bottom: 1.5rem;
  }
  &__user-img {
    width: 5rem;
    flex-shrink: 0;
  }
  &__user-text {
    font-size: 0.875rem;
    line-height: 130%;
    color: #fff;
    strong {
      display: block;
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      color: rgb(245, 216, 155);
      margin-bottom: 0.3125rem;
    }
  }
  &__video {
    padding-bottom: 100%;
    .video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}
