html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}

body {
  font-family: 'NanumGothic', sans-serif;
  background: #0E0E0E;
}

.fixed-body {
  overflow: hidden;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.container-fluid {
  max-width: 1812px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Cormorant';
  font-weight: 700;
  margin: 0;
}


img {
  object-fit: cover;
}