#header {
  background: rgb(14, 14, 14);
  padding: 1.125rem 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.5rem;
  }
  .header__right {
    display: flex;
    align-items: center;
  }
  .header__logo {
    width: 6.5625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__address {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(190, 190, 190);
    margin-right: 4.625rem;
  }
  .header__email {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(162, 162, 162);
    margin-right: 2.5rem;
    a {
      display: block;
      color: rgb(245, 216, 155);
      border-bottom: 0.0625rem dashed rgb(245, 216, 155);
      padding-bottom: 0.1875rem;
      &:hover {
        color: rgb(255, 255, 255);
        border-color: rgb(255, 255, 255);
      }
    }
  }
  .header__socials {
    display: flex;
    gap: 1.125rem;
    margin-right: 4.625rem;
  }
  .header__social {
    position: relative;
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(120.00% 120.00% at 10% 8%,rgb(61, 61, 61),rgb(20, 20, 20) 100%);
    border-radius: 0.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.3125rem);
      height: calc(100% - 0.3125rem);
      border: 0.0625rem solid rgb(245, 216, 155);
      border-radius: 0.3125rem;
      pointer-events: none;
    }
    &:hover {
      background: rgb(71, 71, 71);
    }
    &:active {
      background: rgb(113, 113, 113);
      &:after {
        border-color: rgb(255, 255, 255);;
      }
      svg path {
        fill: rgb(255, 255, 255);
      }
    }
  }
  .instagram {
    &:active {
      background: rgb(113, 113, 113);
      svg g path:nth-child(2) {
        fill: rgb(71, 71, 71);
      }
    }
  }
  .header__group {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
  }
  .header__phone {
    font-family: 'Cormorant';
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255);
    &:hover {
      color: rgb(245, 216, 155);
    }
  }
  .header__btn {
    position: relative;
    background: transparent;
    margin-top: 0.4375rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -0.625rem;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background: rgb(245, 216, 155);
      border-radius: 50%;
      transition: all 0.3s;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -0.9375rem;
      transform: translate(0, -50%);
      width: 1.125rem;
      height: 1.125rem;
      background: rgb(240, 214, 131);
      filter: blur(0.75rem);
      border-radius: 50%;
      pointer-events: none;
    }
    &:hover {
      &:after {
        background: rgb(255, 255, 255);
      }
      span {
        color: rgb(255, 255, 255);
        border-color: rgb(255, 255, 255);
      }
    }
    span {
      min-width: unset;
      font-size: 0.875rem;
      line-height: 120%;
      color: rgb(245, 216, 155);
      border-bottom: 0.0625rem dashed rgb(245, 216, 155);
      padding: 0 0 0.1875rem 0;
      overflow: unset;
      transition: all 0.3s;
    }
  }
}

@media screen and (max-width: 1399px) {
  #header {
    .header__right {
      gap: 2rem;
    }
    .header__address {
      margin-right: 0;
    }
    .header__email {
      margin-right: 0;
    }
    .header__socials {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .header__address {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  #header {
    .header__socials {
      gap: 0.625rem;
    }
  }
}

@media screen and (max-width: 650px) {
  #header {
    .header__email {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  #header {
    .header__inner {
      gap: 1rem;
    }
    .header__right {
      flex-direction: column;
      align-items: flex-end;
      gap: 0.5rem;
    }
    .header__logo {
      width: 5rem;
    }
    .header__socials {
      gap: 0.5rem;
    }
    .header__social {
      width: 2rem;
      height: 2rem;
      svg {
        max-width: 1rem;
      }
    }
    .header__phone {
      font-size: 1.125rem;
    }
  }
}