#footer {
  position: relative;
  z-index: 1;
  padding: 0;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../img/footer-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
}
.footer-top {
  background: rgb(39, 39, 39);
  padding: 4.375rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.5rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.375rem;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 6.5625rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 16.875rem;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 600;
    color: #FFFFFF;
  }
  &__address {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(190, 190, 190);
    margin-right: 4.625rem;
  }
  &__email {
    font-size: 0.875rem;
    line-height: 130%;
    color: rgb(162, 162, 162);
    margin-right: 2.5rem;
    a {
      display: block;
      color: rgb(245, 216, 155);
      border-bottom: 0.0625rem dashed rgb(245, 216, 155);
      padding-bottom: 0.1875rem;
      &:hover {
        color: rgb(255, 255, 255);
        border-color: rgb(255, 255, 255);
      }
    }
  }
  &__socials {
    display: flex;
    gap: 1.125rem;
    margin-right: 4.625rem;
  }
  &__social {
    position: relative;
    width: 2.25rem;
    height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: radial-gradient(120.00% 120.00% at 10% 8%,rgb(61, 61, 61),rgb(20, 20, 20) 100%);
    border-radius: 0.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 0.3125rem);
      height: calc(100% - 0.3125rem);
      border: 0.0625rem solid rgb(245, 216, 155);
      border-radius: 0.3125rem;
      pointer-events: none;
    }
    &:hover {
      background: rgb(71, 71, 71);
    }
    &:active {
      background: rgb(113, 113, 113);
      &:after {
        border-color: rgb(255, 255, 255);;
      }
      svg path {
        fill: rgb(255, 255, 255);
      }
    }
  }
  .instagram {
    &:active {
      background: rgb(113, 113, 113);
      svg g path:nth-child(2) {
        fill: rgb(71, 71, 71);
      }
    }
  }
  &__group {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
  }
  &__phone {
    font-family: 'Cormorant';
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(255, 255, 255) !important;
    &:hover {
      color: rgb(245, 216, 155) !important;
    }
  }
  &__btn {
    position: relative;
    background: transparent;
    margin-top: 0.4375rem;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: -0.625rem;
      transform: translate(0, -50%);
      width: 0.5rem;
      height: 0.5rem;
      background: rgb(245, 216, 155);
      border-radius: 50%;
      transition: all 0.3s;
    }
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: -0.9375rem;
      transform: translate(0, -50%);
      width: 1.125rem;
      height: 1.125rem;
      background: rgb(240, 214, 131);
      filter: blur(0.75rem);
      border-radius: 50%;
      pointer-events: none;
    }
    &:hover {
      &:after {
        background: rgb(255, 255, 255);
      }
      span {
        color: rgb(255, 255, 255);
        border-color: rgb(255, 255, 255);
      }
    }
    span {
      min-width: unset;
      font-size: 0.875rem;
      line-height: 120%;
      color: rgb(245, 216, 155);
      border-bottom: 0.0625rem dashed rgb(245, 216, 155);
      padding: 0 0 0.1875rem 0;
      overflow: unset;
      transition: all 0.3s;
    }
  }
}
.footer-bottom {
  background-color: #000000;
  padding: 3rem 0;
  font-size: 0.875rem;
  line-height: 130%;
  color: rgb(255, 255, 255);
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1rem 1.875rem;
  }
  .b2b {
    width: 11.8125rem;
    img {
      width: 100%;
    }
  }
  &__info {
    a {
      color: rgb(245, 216, 155);
      border-bottom: 0.0625rem dashed rgb(245, 216, 155);
      margin-top: 0.3125rem;
    }
    a:hover {
      color: rgb(255, 255, 255);
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.625rem;
    p {
      margin: 0;
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer-top {
    padding: 3rem 0;
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__right {
      gap: 2rem;
    }
    &__address {
      margin-right: 0;
    }
    &__email {
      margin-right: 0;
    }
    &__socials {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer-top {
    &__text {
      display: none;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    padding: 2rem 0;
    &__address {
      display: none;
    }
  }
  .footer-bottom {
    padding: 2rem 0;
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__socials {
      gap: 0.625rem;
    }
  }
}

@media screen and (max-width: 650px) {
  .footer-top {
    &__email {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__inner {
      gap: 1rem;
    }
    &__right {
      flex-direction: column;
      align-items: flex-end;
      gap: 0.5rem;
    }
    &__logo {
      width: 5rem;
    }
    &__socials {
      gap: 0.5rem;
    }
    &__social {
      width: 2rem;
      height: 2rem;
      svg {
        max-width: 1rem;
      }
    }
    &__phone {
      font-size: 1.125rem;
    }
  }
}